<template>
  <v-card elevation="0" class="mx-auto" color="#F2F4F4" height="100vh">
    <v-container>
      <v-card-title class="text-h5" style="color: #273746">
        No conformidades - General
      </v-card-title>

      <v-textarea
        v-model="result"
        auto-grow
        filled
        color="primary"
        label="Observaciones"
        rows="1"
      ></v-textarea>
      <v-card-actions>
        <v-spacer></v-spacer>
        <!--  <v-btn color="#273746" text @click="start()" disabled> Cancelar </v-btn> -->
        <v-btn outlined color="#273746" @click="update()"> Guardar </v-btn>
      </v-card-actions>
    </v-container>
  </v-card>
</template>
<script>
import { mapState, mapActions } from "vuex";
//import BoardNew from "./BoardNew.vue";

import ItemGroup from "@/components/item/ItemGroup.vue";
export default {
  components: { ItemGroup },
  name: "GeneralNonconformities",
  data() {
    return {
      result: "",
    };
  },
  computed: {
    ...mapState({
      data: (state) => state.customers.findCustomer,
    }),
    config() {
      return this.data.generalNonconformities;
    },
  },
  created() {
    this.init();
  },
  //Aqui se hacen las operaciones de crear, actualizar y eliminar -- pero hasta el final..
  methods: {
    ...mapActions({
      saveItem: "customers/save",
    }),
    init() {
      this.result = this.config;
    },
    update() {
      console.log(this.result);

     let temp = this.data;
     temp.generalNonconformities = this.result;
      //temp.generalNonconformities = this.config

      this.saveItem(temp);
    },
  },
};
</script>